.messageBox{
    /* width: 35%; */
    padding: .6vmax;
    margin: 1vmax;
    border-radius: 6px;
    display: inline-block;
    clear: both;
    font-size: 14px;
}
.right{
    float: right;
    background-color: aquamarine;
    color: black;
}
.left{
    float: left;
    background-color: rgb(110, 255, 115);
}
@media(max-aspect-ratio:7/10){
    .messageBox{
        /* width: 56%; */
        padding: 2vmax;
        margin: 1vmax;
        border-radius: 6px;
        font-size: 20px;
    }
    .right{
        float: right;
        background-color: aquamarine;
        color: black;
        text-align: right;
    }
    .left{
        float: left;
        background-color: rgb(110, 255, 115);
        text-align: left;
    }
}